.homepage{
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
}

.topNav{
    display: flex;
    justify-content: space-between;
}

.topNav--left{
    display: flex;
    max-width: 40%;
    justify-content: space-between;
}
.sideBarBtn{
    margin: auto;
    margin-left: 1em;
    margin-top: 1em;
}
.logo--small{
    margin:auto;
    margin-left: 1em;
    margin-top: 0.2em;
}
.logo--text{
    color: #4749A1;
    font-size: 2em;
    margin-left: 0.3em;
    margin-top: 0.5em;
}


.picture--profile{
    display: flex;
    justify-content: flex-end;
    margin: auto;
    margin-right: 1em;
    margin-top: 0.2em;
}

.heroImage{
    background-color:#FDD2DF;
    padding:4.5em;
    margin: 0 1.8em;
    border-radius: 30px;
}

.features--bar{
    display: flex;
    justify-content: space-around;
    margin: 1.5em 1em;
}
.feature--btn {
    background-color:#F3F2F1;
    padding:0.5em;
    border-radius: 50%;
    width: 2em;
    height: 2em;
}
.feature--text{
    font-weight: 700;
}

.recently--video{
    margin-top: 4em;
}